@import url('https://fonts.googleapis.com/css2?family=Playfair:wght@700;800&family=Poppins:wght@300;400&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}




/* font-family: 'Playfair', serif; */
/* font-family: 'Poppins', sans-serif; */



/* ========footer=================== */

.footer{
  background-color: rgb(58, 56, 56);
  color: white;
  text-align: center;
  padding: 20px 0px;
}

.footer a{
text-decoration: none;
color: white;
padding: 10px;
}


/* ==============navbar==================== */


.navbar{
  box-shadow: 2px 2px 10px rgba(0.5,0.5,0.5,0.7);
}

.active{
  border-bottom: 1px solid black;
}


/* page Not Found================== */


.pnf{
  display: flex;
  flex-direction: column;
  justify-content: center ;
  align-items: center;
}

.pnf span{
  margin-top: 20px;
  font-size: 7rem;
  font-weight: 900;
}

.pnf_button{
  text-decoration: none;
  color: black;
  border: 1px solid black;
  padding: 10px 20px;
  margin-top: 10px;
}

/* =========Policy=========== */


.policy{
  display: flex;
  align-items: center;
}

.policy img{
  margin-top: 30px;
  width: 500px;
  height: auto;
}

.policy .right{
  margin-left: 50px;
}


/* About US============ */



.about{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.about img{
  margin-top: 30px;
  width: 600px;
  height: auto;
}

.about .right{
  margin-left: 50px;
}


/* contact us================== */

.contact{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.contact img{
  margin-top: 30px;
  width: 600px;
  height: auto;
}

.contact .right{
  margin-left: 50px;
}

/* =================Register================= */

.register{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 80vh;
}


/* ------------------------ */
.product-link{
  text-decoration: none !important;
  color: black !important;
}